.title {
  margin-top: 3rem;
  margin-bottom: 3rem;
  width: 100%;

  font-family: 'Prompt', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
}

.condition {
  padding-left: 2rem;

  width: 100%;

  font-size: 2rem;
  font-weight: 300;
  text-align: left;

  word-break: break-word;
}

.times {
  display: flex;
  flex-flow: row;
  justify-content: center;

  padding-bottom: 3rem;
  margin-bottom: 3rem;

  border-bottom: 1px solid #dddddd;
  width: 100%;

  > .time {
    display: flex;
    align-items: center;

    border-radius: 4px;
    border: 2px solid #5D73B3;
    padding: 1.5rem;
    max-width: 360px;
    width: 100%;

    color: #5D73B3;
    font-size: 2rem;

    background: white;

    &:first-child {
      margin-right: 1rem;
    }

    &.active {
      color: white;
      background: #5D73B3;
    }

    &[disabled] {
      opacity: .5;
      cursor: not-allowed;
    }

    i {
      margin-right: 2rem;
      font-size: 4rem;
    }

    .text {
      display: inline-block;
      text-align: left;
    }

    .seat {
      font-size: 2.5rem;
    }
  }
}

.confirm {
  display: block;
  margin: 0 auto;

  border-radius: 4px;
  border: 0;
  padding: 1.5rem;
  max-width: 360px;
  width: 100%;

  color: white;
  font-size: 2rem;

  background-color: #5D73B3;

  &[disabled] {
    opacity: .5;
    cursor: not-allowed;
  }
}

@media (max-width: 768px) {
  .times {
    display: block;

    > .time:first-child{
      margin-bottom: 1rem;
    }
  }

  .condition {
    margin-bottom: 3rem;
    padding-left: 0;
    max-width: 100%;

    font-size: 1.6rem;
    line-height: 1.6;
    text-indent: 3rem;
  }
}



