@import '../../theme/variables.scss';

.container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;

  padding-top: $section-padding;
  padding-bottom: $section-padding;
}

.vips,
.partners,
.sponsors {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 0 0 4rem;
}

.vips {
  > a > .sponsor {
    height: 25rem;
  }
}

.partners {
  > a > .sponsor {
    height: 18rem;
  }
}

.sponsors {
  > a > .sponsor {
    height: 14rem;
  }
}

.sponsor {
  border-radius: 50%;
}

@media (max-width: 768px) {
  .vips,
  .partners,
  .sponsors {
    justify-content: center;

    margin: 0 0 .5rem;

    > a > .sponsor {
      margin: .5rem;
    }
  }

  .vips {
    > a > .sponsor {
      height: 120px
    }
  }

  .partners {
    > a > .sponsor {
      height: 100px;
    }
  }

  .sponsors {
    > a > .sponsor {
      height: 75px;
    }
  }
}



