.title,
.name {
  margin: 0;

  font-family: 'Prompt', sans-serif;
  font-weight: lighter;
}

.title {
  border-bottom: 1px solid #dddddd;
  margin-bottom: 2rem;
  padding-bottom: 2rem;

  font-size: 3rem;
}

.avatar {
  margin: 0 0 2rem;

  height: 128px;
}

.name {
  margin: 0 0 2rem;

  width: 100%;

  color: #5D73B3;
  font-size: 2.5rem;
  text-align: center;
}

.detail {
  margin: 0 auto 2rem;
  max-width: 480px;

  font-size: 1.5rem;
  font-weight: lighter;
}

.actions {
  width: 100%;

  text-align: center;

  > .notMe {
    position: relative;
    border: 1px solid white;
    padding: 2rem;

    font-family: 'Prompt', sans-serif;
    font-size: 2rem;
    width: 40%;

    > .helper {
      position: absolute;
      left: 0;
      right: 0;
      bottom: .5rem;

      font-family: sans-serif;
      font-weight: 300;
      font-size: 1.4rem;
    }
  }

  > .notMe {
    display: inline-block;
    border-color: #5D73B3;

    color: white;
    background-color: #5D73B3;

    &:hover {
      background-color: darken(#5D73B3, 15%);
    }
  }
}

@media (max-width: 768px) {
  .actions {
    > .notMe {
      width: 100%;
    }

    > .notMe {
      margin-right: 0;
      margin-bottom: 2rem;
    }
  }
}



