.popup_wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(102, 102, 102, 0.7);
  z-index: 1000;
}

.popup {
  width: 80%;
}

.exit {
  position: absolute;
  top: 50px;
  right: 8%;
  z-index: 100;
  color: white;
  font-size: 30px;
  cursor: pointer;
}

@media (max-width: 1000px) {
  .popup {
    width: 90%;
  }
}

@media (max-width: 800px) {
  .popup {
    width: 100%;
  }
}



