.register_wrapper {
  width: 100%;
  background: #4d5d9d; /* Old browsers */
  background: -moz-linear-gradient(top,  #4d5d9d 0%, #97d8f4 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top,  #4d5d9d 0%,#97d8f4 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom,  #4d5d9d 0%,#97d8f4 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  background-size: 100% 600px;
  background-repeat: no-repeat;
  margin-bottom: 50px;
  font-family: Prompt, sans-serif;
  label{
    font-weight: normal;
  }
}

.register {
  background: white;
  border-top: 10px solid #97d8f4;
  padding: 5rem;

  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.09);
}

.progress {
  overflow: visible;
  margin-bottom: 90px;
  height: 20px;
  margin-top: 120px;
  width: 100%;
  border-radius: 10px;
  background: #f5f5f5;
  .progress_bar {
    background-color: #1daeff;
    position: relative;
    border-radius: 10px;
    height: 20px;
    span {
      background-color: #363636;
      color: white;
      position: absolute;
      bottom: -35px  ;
      font-size: 20px;
      line-height: 10px;
      right: -1em;
      border-radius: 5px;
      width: 60px;
      height: 30px;
      text-align: center;
      padding-top: 10px;
    }
    span:after {
      bottom: 100%;
      left: 50%;
      border: solid transparent;
      content: " ";
      height: 0;
      width: 0;
      position: absolute;
      pointer-events: none;
      border-color: rgba(255, 255, 255, 0);
      border-bottom-color: #363636;
      border-width: 5px;
      margin-left: -5px;
    }
  }
}

:global {
  .form-name {
    font-weight: normal;
    margin: 0 0 3rem;
  }
}



