.title {
  margin-top: 3rem;
  margin-bottom: 3rem;
  width: 100%;

  font-family: 'Prompt', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
}

.inputGroup {
  margin: 0 0 4rem 0;

  max-width: 100%;
  width: 640px;

  > .label {
    margin: 0;
    display: block;

    font-size: 2rem;
    font-family: 'Prompt', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: normal;
  }

  > .remark {
    margin: 0 0 .5rem 0;

    font-size: 1.5rem;
    font-weight: lighter;
  }

  > .input,
  > .textArea {
    border-radius: 2px;
    border: 1px solid #ccc;
    padding: .5rem .8rem;
    max-width: 100%;
    width: 100%;
    height: auto;

    font-size: 1.5rem;

    &:active,
    &:focus {
      outline: 0;
      border: 1px solid #5D73B3;
    }
  }

  > .input.phone {
    width: 120px;
  }

  > .input.email {
    width: 200px;
  }

  > .textArea {
    min-height: 150px;
  }
}

.confirm {
  display: block;
  margin: 0 auto;

  border-radius: 4px;
  border: 0;
  padding: 1.5rem;
  max-width: 360px;
  width: 100%;

  color: white;
  font-size: 2rem;

  background-color: #5D73B3;

  &:hover {
    background-color: darken(#5D73B3, 15%);
    text-decoration: none;
    color: white;
  }

  &[disabled] {
    opacity: .5;
    cursor: not-allowed;
  }
}

