$arrow-size: 15px;
$dot-positon: 60px;

.container {
  padding-top: 65px;
  padding-bottom: 65px;
}

.title {
  margin-bottom: 40px;
  text-align: center;
}

.dots {
  position: absolute;
  bottom: -$dot-positon;
  left: 0;
  right: 0;

  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;

  > li {
    display: inline-block;

    > button {
      border: 0;
      border-radius: 18px;
      padding: 0;
      width: 18px;
      height: 18px;

      background-color: #D8DBE4;

      font-size: 0;
      line-height: 0;

      &:active,
      &:focus {
        outline: none;
      }
    }

    &:not(:last-child) {
      margin: 0 10px 0 0;
    }
  }
}

:global {
  li.slick-active {
    > button {
      background-color: #5769AC;
    }
  }
}

.faq {
  padding: 0 15rem;

  .question,
  .answer {
    position: relative;
    border-radius: 15px;
    padding: 30px 40px;
    line-height: 1.8;
    font-weight: 300;

    &:before {
      content: " ";
      display: block;
      position: absolute;
      bottom: $arrow-size + 10;

      width: 0;
      height: 0;
      border-top: $arrow-size / 2 solid transparent;
      border-bottom: $arrow-size / 2 solid transparent;
    }

    &:after {
      content: " ";
      display: block;

      position: absolute;
      bottom: 0;

      width: 7rem;
      height: 7rem;

      background-size: contain;
      background-repeat: no-repeat;
    }
  }

  .question {
    margin-bottom: 25px;

    background-color: #D8DBE4;
    color: #444444;

    &:before {
      left: -$arrow-size;
      border-right: $arrow-size solid #D8DBE4;
    }

    &:after {
      left: -9rem;
      background-image: url(./user.png);
    }
  }

  .answer {
    background-color: #5769AC;
    color: white;

    &:before {
      right: -$arrow-size;
      border-left: $arrow-size solid #5769AC;
    }

    &:after {
      right: -9rem;
      background-image: url(./ywc.png);
    }
  }
}

:global {
  .slick-slider {
    margin-bottom: $dot-positon;
  }

  .slick-next:before, .slick-prev:before {
    font-size: 42px !important;
    color: #D8DBE4 !important;

    &:hover {
      color: #5769AC !important;
    }
  }

  .slick-arrow {
    width: 42px !important;
    height: 42px !important;
  }
}

@media (max-width: 768px) {
  .container {
    .faq {
      padding: 0 15px;

      .question,
      .answer {
        padding: 15px;

        &:after {
          display: none;
        }
      }
    }
  }

  :global {
    .slick-slider {
      margin-bottom: $dot-positon;
    }

    .slick-next:before, .slick-prev:before {
      font-size: 42px !important;
      color: #D8DBE4 !important;

      &:hover {
        color: #5769AC !important;
      }
    }

    .slick-next:before, .slick-prev:before {
      display: none;
    }
  }
}



