.title,
.name {
  margin: 0;

  font-family: 'Prompt', sans-serif;
  font-weight: lighter;
}

.title,
.stepTitle {
  border-bottom: 1px solid #dddddd;
  margin-bottom: 2rem;
  padding-bottom: 2rem;

  font-size: 3rem;
}

.stepTitle {
  width: 100%;

  font-family: 'Prompt', sans-serif;
  font-size: 1.8rem;
  text-align: center;
}

.avatar {
  margin: 0 0 2rem;

  height: 128px;
}

.name {
  margin: 0 0 .5rem;

  width: 100%;

  color: #5D73B3;
  font-size: 3.5rem;
  text-align: center;
}

.detail {
  width: 100%;
  margin: 2rem auto 1rem;

  font-size: 2.2rem;
  font-weight: lighter;
  text-align: center;
}

.steps {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-bottom: 2rem;

  > .step {
    padding: 2rem;
    flex: 0 1 250px;

    > .img {
      margin-bottom: 1rem;
      width: 100%;
      max-width: 240px;
    }

    > .title {
      font-size: 1.5rem;
      text-align: center;
      font-weight: 300;
    }
  }
}

.actions {
  width: 100%;

  text-align: center;

  > .notMe,
  > .confirm {
    border: 1px solid white;
    padding: 2rem;

    font-family: 'Prompt', sans-serif;
    font-size: 2rem;
    width: 40%;
  }

  > .notMe {
    position: relative;
    margin: 0 1rem 0 0;

    border-color: #5D73B3;

    color: #5D73B3;

    background: none;

    &:hover {
      background-color: #5D73B3;
      color: white;
    }

    > .helper {
      position: absolute;
      left: 0;
      right: 0;
      bottom: .5rem;

      font-family: sans-serif;
      font-weight: 300;
      font-size: 1.4rem;
    }
  }

  > .confirm {
    display: inline-block;
    border-color: #5D73B3;

    color: white;
    background-color: #5D73B3;

    &:hover {
      background-color: darken(#5D73B3, 15%);
    }
  }
}

.major {
  margin-top: 0;
  margin-bottom: 1rem;
}

.design {
  color: #E9AB2B;
}

.programming {
  color: #6CC0B6;
}

.content {
  color: #5D73B3;
}

.marketing {
  color: #D66C64;
}

@media (max-width: 768px) {
  .actions {
    > .notMe,
    > .confirm {
      width: 100%;
    }

    > .notMe {
      margin-right: 0;
      margin-bottom: 2rem;
    }
  }
}



