@import '../../theme/variables.scss';

.container {
  padding-bottom: $section-padding;
}

.title {
  margin-bottom: 40px;
  text-align: center;
}

.timelines {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.timeline {
  position: relative;

  margin: 0 13px;

  flex: 1 1 350px;
  width: 350px;

  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;

  color: #cccccc;
  font-family: Prompt, sans-serif;
  text-align: center;

  > .image {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;

    height: 170px;

    > img {
      width: auto;
      height: 65%;
    }
  }

  > .title {
    margin: 20px 0 10px;
    font-weight: normal;
  }

  > .subText {
    margin: 0 0 1rem;
  }

  > .subTitle {
    margin: 0;
    font-weight: lighter;
  }

  &.current {
    color: #444444;
    > .image > img { height: 100% }
  }
}

@media (max-width: 768px) {
  .timelines {
    flex-flow: column nowrap;

    .timeline {
      margin-bottom: 40px;
      width: auto;
      flex: auto;
    }
  }
}



