.personal,
.section {
  width: 640px;
  max-width: 100%;
}

.avatar {
  margin: 0 auto 3rem;
  width: 200px;
  height: 200px;

  flex: 0 0 200px;
  background-size: contain;
  background-repeat: no-repeat;
}

.header {
  margin-bottom: 2rem;

  padding-bottom: 1rem;
  border-bottom: 1px solid #dddddd;

  font-family: 'Prompt', sans-serif;
  font-weight: 300;
}

.detailGroup {
  margin: 0 0 3rem;

  color: #444;
  line-height: 1.7;

  > .label {
    margin: 0 0 .5rem;

    font-weight: normal;
    line-height: 1.7;
    word-wrap: break-word;
    white-space: normal;
  }

  > .detail {
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.7;
  }
}

@media (min-width: 768px) {
  .section {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: flex-start;

    > .header {
      flex: 0 0 100%;
      width: 100%;
    }
  }

  .detailGroup {
    flex-grow: 1;
    margin-right: 2rem;
  }

  .info {
    display: flex;
    flex-flow: row nowrap;

    > .avatar {
      margin-right: 2rem;
    }

    > .detailGroup > .label {
      font-size: 3rem;
    }

    > .detailGroup > .detail {
      font-size: 2.7rem;
    }
  }
}



