.container {
  padding-top: 65px;
  padding-bottom: 65px;
}

.title {
  margin-bottom: 40px;
  text-align: center;
}

.dots {
  position: absolute;
  bottom: -60px;
  left: 0;
  right: 0;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
}

.dots > li {
  display: inline-block;
}

.dots > li > button {
  border: 0;
  border-radius: 18px;
  padding: 0;
  width: 18px;
  height: 18px;
  background-color: #D8DBE4;
  font-size: 0;
  line-height: 0;
}

.dots > li > button:active, .dots > li > button:focus {
  outline: none;
}

.dots > li:not(:last-child) {
  margin: 0 10px 0 0;
}

:global li.slick-active > button {
  background-color: #5769AC;
}

.faq {
  padding: 0 15rem;
}

.faq .question,
.faq .answer {
  position: relative;
  border-radius: 15px;
  padding: 30px 40px;
  line-height: 1.8;
  font-weight: 300;
}

.faq .question:before,
.faq .answer:before {
  content: " ";
  display: block;
  position: absolute;
  bottom: 25px;
  width: 0;
  height: 0;
  border-top: 7.5px solid transparent;
  border-bottom: 7.5px solid transparent;
}

.faq .question:after,
.faq .answer:after {
  content: " ";
  display: block;
  position: absolute;
  bottom: 0;
  width: 7rem;
  height: 7rem;
  background-size: contain;
  background-repeat: no-repeat;
}

.faq .question {
  margin-bottom: 25px;
  background-color: #D8DBE4;
  color: #444444;
}

.faq .question:before {
  left: -15px;
  border-right: 15px solid #D8DBE4;
}

.faq .question:after {
  left: -9rem;
  background-image: url(./user.png);
}

.faq .answer {
  background-color: #5769AC;
  color: white;
}

.faq .answer:before {
  right: -15px;
  border-left: 15px solid #5769AC;
}

.faq .answer:after {
  right: -9rem;
  background-image: url(./ywc.png);
}

:global .slick-slider {
  margin-bottom: 60px;
}

:global .slick-next:before, :global .slick-prev:before {
  font-size: 42px !important;
  color: #D8DBE4 !important;
}

:global .slick-next:before:hover, :global .slick-prev:before:hover {
  color: #5769AC !important;
}

:global .slick-arrow {
  width: 42px !important;
  height: 42px !important;
}

@media (max-width: 768px) {
  .container .faq {
    padding: 0 15px;
  }
  .container .faq .question,
  .container .faq .answer {
    padding: 15px;
  }
  .container .faq .question:after,
  .container .faq .answer:after {
    display: none;
  }
  :global .slick-slider {
    margin-bottom: 60px;
  }
  :global .slick-next:before, :global .slick-prev:before {
    font-size: 42px !important;
    color: #D8DBE4 !important;
  }
  :global .slick-next:before:hover, :global .slick-prev:before:hover {
    color: #5769AC !important;
  }
  :global .slick-next:before, :global .slick-prev:before {
    display: none;
  }
}



