.header {
  overflow: hidden;
  position: relative;
  padding: calc(170px + 4rem) 0 0;
  background: #4d5d9d;
  /* Old browsers */
  background: -moz-linear-gradient(top, #4d5d9d 0%, #97d8f4 100%);
  /* FF3.6-15 */
  background: webkit-linear-gradient(top, #4d5d9d 0%, #97d8f4 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #4d5d9d 0%, #97d8f4 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

.header > .container {
  position: relative;
  z-index: 100;
}

.header > .pattern {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: url(./bg-pattern.png);
  background-repeat: repeat;
  background-size: 200px;
  z-index: 1;
  transform: rotateZ(-5deg) scale(1.2);
  pointer-events: none;
}

.container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  border-top: 8px solid #97d8f4;
  border-radius: 4px 4px 0 0;
  padding-top: 4rem;
  padding-bottom: 4rem;
  background-color: #fbfbfb;
}

.steps {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-bottom: 2rem;
}

.steps > .step {
  padding: 2rem;
  flex: 0 1 250px;
}

.steps > .step > .img {
  width: 100%;
}

.steps > .step > .title {
  font-weight: 300;
}

@media (max-width: 768px) {
  .header {
    padding: calc(50px + 2rem) 0 0;
  }
  .header > .pattern {
    transform: rotateZ(0);
  }
}



