.appBar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: white;
  z-index: 9999;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);
}

.container {
  height: 95px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  font-family: Kanit, sans-serif;
  transition: all .4s ease-in-out;
}

.container .logo {
  flex: 1 0 100px;
  width: 100px;
  max-width: 200px;
  align-self: flex-start;
  transition: all .4s ease-in-out;
}

.container .logo > img {
  width: 100%;
}

.container .link {
  align-self: stretch;
  flex: 1 0 130px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 130px;
  color: #464748;
  text-align: center;
  cursor: pointer;
}

.container .link .desktop {
  display: block;
}

.container .link .mobile {
  display: none;
}

@media (min-width: 768px) {
  .container.shrink {
    height: 50px;
  }
  .container.shrink .logo {
    flex: 0 0 100px;
    align-self: flex-start;
  }
  .container.shrink .logo > img {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .container {
    height: 50px;
  }
  .container .logo {
    margin-right: 15px;
    flex: 0 0 100px;
    width: 100px;
    align-self: flex-start;
  }
  .container .logo > img {
    width: 100%;
  }
  .container .link {
    font-size: 10px;
    flex: auto;
    text-overflow: ellipsis;
  }
  .container .link .desktop {
    display: none;
  }
  .container .link .mobile {
    display: block;
  }
  .container .link:not(:last-child) {
    margin-right: 15px;
  }
}



