.button {
  display: flex;
  flex-direction: row;
  align-items: center;

  margin: 0 auto;
  border: 0;
  border-radius: 4px;
  padding: 12px 20px;

  color: white;
  background-color: #3b5998;
  box-shadow: 0px 3px 1px #DBDEE1;

  > .icon {
    font-size: 2.5rem;
    margin-left: 15px;
  }
}



