@import '../../theme/variables.scss';

.container {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;

  padding-top: $section-padding;
  padding-bottom: $section-padding;

  .image {
    max-width: 400px;
  }
}

.textContainer {
  padding: 0 10rem 0 5rem;

  .title,
  .subTitle {
    font-family: Kanit, sans-serif;
  }

  .title {
    margin: 0 0 .5rem;
  }

  .subTitle {
    margin: 0 0 3rem;

    color: #B5B8B9;
  }

  .description {
    margin-bottom: 30px;

    color: #666;
    font-weight: 300;
    text-indent: 3rem;
    line-height: 1.6;
  }
}

@media (max-width: 768px) {
  .container {
    flex-flow: column nowrap;

    .image {
      margin: 0 0 2rem;
      max-width: 100%;
      order: 1;
    }
  }

  .textContainer {
    padding: 0 1rem;
    order: 2;
  }
}



