@import '../../theme/variables.scss';

$block-width: 33.3333%;
$programmer: #6EC2B3;
$content: #6177B7;
$design: #E0B12E;
$marketing: #D5665D;

.container {
  display: flex;
  flex-flow: column nowrap;

  color: #fff;
  background-color: #2F303F;
}

.row {
  display: flex;
  flex-flow: row nowrap;
  flex-grow: 0;
  font-family: Prompt, sans-serif;
}

.title {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;

  flex: 1 0 $block-width;
  width: $block-width;
}

.guru {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;

  flex: 1 0 $block-width;
  width: $block-width;

  text-align: center;
}

.guruImage {
  width: 50%;
  height: 100%;
}

.guruProfile {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;

  width: 50%;
  height: 100%;
  padding: 1.5rem;

  text-align: center;

  > .name {
    margin: 0 0 1rem;
  }

  > .profile {
    margin: 0;

    font-weight: 300;
    line-height: 1.6;
  }
}

@media (max-width: 768px) {
  .row {
    flex-flow: row wrap;
    order: 2;

    &.middle {
      order: 1;

      > .guru {
        > .guruImage {
          order: 1;
        }

        > .guruProfile {
          order: 2;
        }
      }
    }
  }

  .guru {
    flex: 0 0 100%;
    order: 2;
  }

  .title {
    order: 1;
  }
}

.programmer {
  background: $programmer;
}

.content {
  background: $content;
}

.design {
  background: $design;
}

.marketing {
  background: $marketing;
}

:global {
  .guruAnimate-enter {
    opacity: 0.01;
  }

  .guruAnimate-enter.guruAnimate-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
  }

  .guruAnimate-leave {
    opacity: 1;
  }

  .guruAnimate-leave.guruAnimate-leave-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in;
  }
}



