.container {
  position: relative;
}

.button {
  width: 100%;

  outline: 0;
  border-radius: 4px;
  border: 0;

  padding: .7rem 0;
  color: white;

  cursor: pointer;
  background-color: #6177B7;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
}

.datePicker {
  position: absolute;
  top: calc(100% + 2rem);

  z-index: 100;
}



