@import '../../theme/variables.scss';

.container {
  padding-top: $section-padding / 2;
  padding-bottom: $section-padding / 2;

  background-color: #F3F8F8;

  :global {
    .section-title {
      color: #515555;
    }
  }
}

.socials {
  margin: 0 0 2rem;

  display: flex;
  flex-flow: row nowrap;
  justify-content: center;

  > .social {
    display: block;
    margin: 0 5rem;
    width: 70px;
    height: 70px;

    > img {
      max-width: 100%;
    }
  }
}

@media (max-width: 768px) {
  .socials {
    .social {
      width: 48px;
      height: 48px;
      margin: 0 1rem;
    }
  }
}



