.mastHead {
  position: relative;

  background: #4d5d9d; /* Old browsers */
  background: url(./bg.png), -moz-linear-gradient(top,  #4d5d9d 0%, #97d8f4 100%); /* FF3.6-15 */
  background: url(./bg.png), -webkit-linear-gradient(top,  #4d5d9d 0%,#97d8f4 100%); /* Chrome10-25,Safari5.1-6 */
  background: url(./bg.png), linear-gradient(to bottom,  #4d5d9d 0%,#97d8f4 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  background-position: center bottom;
  background-clip: border-box;
  background-size: cover;
  background-repeat: no-repeat;

  .arrow {
    display: block;

    position: absolute;
    left: 0;
    right: 0;
    bottom: -45px;

    margin: 0 auto;

    width: 75px;
    height: 90px;
    cursor: pointer;
  }

  .free {
    position: absolute;
    top: 10rem;
    left: 3rem;

    width: 150px;
    height: auto;
  }

  .cta {
    cursor: pointer;

    margin-top: 2rem;

    border-radius: 4px;
    padding: 1rem 2rem;
    min-width: 200px;

    color: #4d5d9d;
    font-size: 3rem;
    font-weight: 300;
    background: white;
  }
}

.container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;

  padding: 200px 0px;

  color: white;
  text-align: center;

  .title {
    position: relative;

    margin: 0 0 2rem;

    font-size: 11rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
  }

  .subTitle {
    margin: 0 0 1rem;
    font-size: 5rem;
    font-family: 'Prompt', sans-serif;
    font-weight: normal;
  }

  .info {
    margin: 0;
    font-size: 2rem;
    font-family: 'Prompt', sans-serif;
    font-weight: normal;
  }
}

.sponsors {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;

  margin-top: 2rem;
  margin-bottom: 1rem;

  padding: 0 50px;
  width: 100%;

  .sponsor {
    flex: 0 1 150px;

    width: 200px;
  }
}

@media (max-width: 768px) {
  .mastHead {
    background-size: contain;

    .arrow {
      width: 35px;
      height: 30px;
      cursor: pointer;
      bottom: -10px;
    }

    .free {
      top: 10px;
      left: 10px;
      width: 64px;
    }
  }
  .container {
    padding: 80px 1rem;

    .title {
      font-size: 6rem;
    }

    .subTitle {
      font-size: 1.8rem;
    }

    .info {
      font-size: 1.2rem;
    }
  }
}



