.section {
  padding-top: 15rem;

  font-family: Prompt, sans-serif;

  background: linear-gradient(to bottom, #4d5d9d 0%, #97d8f4 100%);
  background-size: 100% 600px;
  background-repeat: no-repeat;
}

.container {
  border-top: 10px solid #97d8f4;
  padding: 5rem;

  background: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.09);

  > .title {
    margin: 0 0 3rem;
    text-align: center;
  }

  > .condition {
    font-family: sans-serif;
    font-size: 1.6rem;
    font-weight: 300;
  }

  > .button {
    display: block;
    outline: 0;
    border-radius: 4px;
    border: 0;
    margin: 4rem auto 0 auto;
    padding: 1.4rem 0;
    width: 200px;
    text-align: center;

    color: white;

    cursor: pointer;

    background-color: #6177B7;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
  }
}

@media (max-width: 768px) {
  .container {
    padding: 3rem 1.5rem;

    > .button {
      width: 100%;
    }
  }
}



