.container {
  padding-top: 100px;
}

.headerContainer {
  padding: 4rem 0;
  background: #4d5d9d; /* Old browsers */
  background: url(./bg-pattern.png), -moz-linear-gradient(top,  #4d5d9d 0%, #97d8f4 100%); /* FF3.6-15 */
  background: url(./bg-pattern.png), -webkit-linear-gradient(top,  #4d5d9d 0%,#97d8f4 100%); /* Chrome10-25,Safari5.1-6 */
  background: url(./bg-pattern.png), linear-gradient(to bottom,  #4d5d9d 0%,#97d8f4 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  background-repeat: repeat;
}

.header {
  display: flex;
  flex-flow: row;
  align-items: center;

  height: 250px;

  > .logo {
    margin: 0 4rem 0 0;
    height: 128px;
  }

  > .major {
    color: white;
    font-size: 6rem;
  }
}

.sectionContainer {
  display: flex;
  flex-flow: row;
  align-items: center;

  padding-top: 4rem;
  padding-bottom: 4rem;

  &.isOdd {
    > .image {
      margin: 0;
      margin-left: 4rem;

      order: 2;
    }

    > .textContainer {
      order: 1;
    }
  }

  > .image {
    margin: 0 4rem 0 0;

    width: 20%;
  }

  > .textContainer {
    width: 80%;
  }
}

.textContainer {
  > .quote {
    overflow: visible;
    margin-bottom: 2rem;

    border-left: 3px solid #ddd;
    padding: 10px 20px;

    color: #777;
    font-size: 2rem;
    font-family: Prompt;
    font-weight: 300;
  }

  > .paragraph {
    text-indent: 4rem;

    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.7;
  }
}

.textContainer > .title {
  margin: 0 0 2rem 0;

  padding-top: .8rem;
  padding-bottom: .8rem;
  width: 500px;

  color: white;
  text-align: center;

  &.design {
    background: #E9AB2B;
  }

  &.programming {
    background: #6CC0B6;
  }

  &.content {
    background: #5D73B3;
  }

  &.marketing {
    background: #D66C64;
  }
}

@media (max-width: 600px) {
  .container {
    padding-top: 50px;
  }

  .header {
    display: block;
    text-align: center;

    > .logo {
      margin: 0 0 2rem;
    }

    > .major {
      font-size: 3.3rem;
    }
  }

  .sectionContainer {
    display: block;

    padding-top: 2rem;
    padding-bottom: 2rem;

    > .image {
      display: block;

      margin-left: auto;
      margin-right: auto;

      width: 250px;
    }

    > .textContainer {
      width: 100%;
    }
  }

  .textContainer {
    > .title {
      display: block;

      padding-left: 1rem;
      padding-right: 1rem;
      width: auto;
    }
  }
}



