.section {
  background: #4d5d9d; /* Old browsers */
  background: -moz-linear-gradient(top,  #4d5d9d 0%, #97d8f4 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top,  #4d5d9d 0%,#97d8f4 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom,  #4d5d9d 0%,#97d8f4 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  background-repeat: no-repeat;
}

.container {
  display: flex;
  flex-flow: column;
  padding-top: 5rem;
  padding-bottom: 5rem;

  > .image {
    max-width: 100%;
    margin: 0 0 2rem;
  }
}

.textContainer {
  text-align: center;

  color: white;
  font-weight: 300;

  > .title {
    font-size: 5rem;
    margin: 0 0 3rem;
  }

  > .text {
    font-size: 2rem;
    margin: 0 0 1.5rem;
  }

  > .link {
    display: block;
    border-radius: 4px;
    padding: 1.5rem;
    width: 100%;

    color: #4d5d9d;
    background: white;
    box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);

    &:hover {
      color: white;
      text-decoration: none;
      background: #4d5d9d;
    }
  }
}

@media (min-width: 768px) {
  .container {
    > .image {
      display: block;
      margin: 15rem auto 3rem;
      max-width: 240px;
    }
  }

  .textContainer {
    > .link {
      margin: 0 auto;
      max-width: 200px;
    }
  }
}



