.container {
  padding: 30px;

  color: white;
  font-weight: 300;
  text-align: center;

  background-color: #363638;

  > .logo {
    margin-bottom: 30px;
  }

  > .vr_logo {
    margin-bottom: 30px;

    border: 15px solid white;
    border-top-width: 10px;
    border-bottom-width: 10px;
    height: 55px;
  }

  .heart {
    color: red;
  }
}

.sponsors {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;

  padding: 0 50px;
  width: 100%;

  .sponsor {
    flex: 0 1 100px;

    width: 100px;
  }
}



