@import '../../theme/variables.scss';

.section {
  padding-top: $section-padding;
  padding-bottom: $section-padding;

  background: #98D7F3;
}

.container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.contactContainer {
  border-radius: 5px;
  margin: 0 10rem 0 0;
  padding: 5rem;
  flex-grow: 1;

  background: white;
  box-shadow: 3px 3px 1px #78BEE3;
}

.mobiles,
.email {
  display: flex;
  flex-flow: row nowrap;
  font-family: 'Prompt', sans-serif;

  > .icon {
    width: auto;
    height: 50px;
    margin: 0 3rem 0 0;
  }

  > .content {
    font-weight: lighter;
    margin: 0 0 2.5rem;

    > h4 {
      margin: 1rem 0 1.5rem;
    }

    > .contact {
      margin: 0 0 1rem;
    }
  }
}

.email {
  align-items: center;

  > .content {
    margin: 0;
    height: 100%;
  }
}
.informaion {
  @extend .mobiles;
  margin-top: 40px;
  > .space {
    width: 50px;
    height: 50px;
    margin: 0 3rem 0 0;
  }
  > .link, .link:link {
    text-decoration: none;
    font-weight: bold;
  }
  .content {
    margin-top: auto;
    margin-bottom: auto;
  }
}

@media (max-width: 768px) {
  .container {
    flex-flow: column nowrap;
  }

  .contactContainer {
    margin: 0 0 2rem;
    padding: 2rem;
    width: 100%;
  }
}



