/**
 *  Define scss variables here.
 *
 *  Available options for Bootstrap:
 *  http://getbootstrap.com/customize/
 *
 */
.container {
  display: flex;
  flex-flow: column nowrap;
  color: #fff;
  background-color: #2F303F;
}

.row {
  display: flex;
  flex-flow: row nowrap;
  flex-grow: 0;
  font-family: Prompt, sans-serif;
}

.title {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  flex: 1 0 33.3333%;
  width: 33.3333%;
}

.guru {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  flex: 1 0 33.3333%;
  width: 33.3333%;
  text-align: center;
}

.guruImage {
  width: 50%;
  height: 100%;
}

.guruProfile {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
  padding: 1.5rem;
  text-align: center;
}

.guruProfile > .name {
  margin: 0 0 1rem;
}

.guruProfile > .profile {
  margin: 0;
  font-weight: 300;
  line-height: 1.6;
}

@media (max-width: 768px) {
  .row {
    flex-flow: row wrap;
    order: 2;
  }
  .row.middle {
    order: 1;
  }
  .row.middle > .guru > .guruImage {
    order: 1;
  }
  .row.middle > .guru > .guruProfile {
    order: 2;
  }
  .guru {
    flex: 0 0 100%;
    order: 2;
  }
  .title {
    order: 1;
  }
}

.programmer {
  background: #6EC2B3;
}

.content {
  background: #6177B7;
}

.design {
  background: #E0B12E;
}

.marketing {
  background: #D5665D;
}

:global .guruAnimate-enter {
  opacity: 0.01;
}

:global .guruAnimate-enter.guruAnimate-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

:global .guruAnimate-leave {
  opacity: 1;
}

:global .guruAnimate-leave.guruAnimate-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}



