.link {
  display: block;
  
  margin: 0 0 2rem;

  color: #444;
  font-family: Prompt, sans-serif;
  font-size: 1.8rem;

  &:hover {
    text-decoration: none;
  }
}

.design {
  .link:hover {
    color: #E9AB2B;
  }
}

.programming {
  .link:hover {
    color: #6CC0B6;
  }
}

.content {
  .link:hover {
    color: #5D73B3;
  }
}

.marketing {
  .link:hover {
    color: #D66C64;
  }
}

.container.marketing {
  > .image {
    padding: 0 35px;
  }
}
.container.programming {
  > .image {
    padding: 0 6px;
  }
}
.container {
  padding: 0 20px;
  flex: 1 1 300px;
  width: 300px;

  text-align: center;

  > .image {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    margin: 0 0 50px;
    padding: 0 30px;

    > img {
      width: auto;
      height: 200px;
    }
  }

  > .name {
    margin: 0 0 40px;
    font-size: 20px;
  }

  > .counter {
    margin: 0 0 40px;
    font-size: 26px;
  }

  > .detail {
    margin: 0 0 25px;

    font-size: 16px;
    font-weight: 300;
  }

  > .button {
    border: 0;
    border-radius: 4px;
    padding: 12px 20px;

    background-color: white;
    box-shadow: 0px 3px 1px #DBDEE1;

    > .text {
      margin: 0 15px;
      font-family: Kanit, sans-serif;
    }

    > .mainIcon.icon.icon {
      border-radius: 50%;
      height: 25px;
      width: 25px;

      color: white;
      font-size: 12px;
      text-align: center;
      line-height: 25px;
    }

    > .icon {
      color: #DDD;
    }

    &:hover {
      > .icon {
        color: white;
      }

      > .mainIcon.icon.icon {
        background-color: white;
      }
    }
  }

  &.content {
    > .name,
    > .counter {
      color: #5D73B3;
    }

    .mainIcon.icon {
      background-color: #5D73B3;
    }

    > .button:hover {
      color: white;
      background-color: #5D73B3;

      .mainIcon.icon {
        color: #5D73B3;
      }
    }
  }

  &.design {
    > .name,
    > .counter {
      color: #E9AB2B;
    }

    .mainIcon.icon {
      background-color: #E9AB2B;
    }

    > .button:hover {
      color: white;
      background-color: #E9AB2B;

      .mainIcon.icon {
        color: #E9AB2B;
      }
    }
  }

  &.marketing {
    > .name,
    > .counter {
      color: #D66C64;
    }

    .mainIcon.icon {
      background-color: #D66C64;
    }

    > .button:hover {
      color: white;
      background-color: #D66C64;

      .mainIcon.icon {
        color: #D66C64;
      }
    }

  }

  &.programming {
    > .name,
    > .counter {
      color: #6CC0B6;
    }

    .mainIcon.icon {
      background-color: #6CC0B6;
    }

    > .button:hover {
      color: white;
      background-color: #6CC0B6;

      .mainIcon.icon {
        color: #6CC0B6;
      }
    }


  }
}


@media (max-width: 768px) {
  .container {
    margin-right: auto;
    margin-left: auto;
    padding: 0 15px;

    width: auto;
    flex: auto;

    .image {
      text-align: center;
    }
  }

  .container:not(:last-child) {
    margin-bottom: 40px;
  }
}



