.container {
  display: flex;
  flex-flow: column wrap;
  justify-content: flex-start;
  align-items: center;

  max-height: 350px;

  > .button {
    margin: 0 0 3rem;
    padding: 1rem 3rem;
    border: 1px solid #444444;
    color: #444;
  }

  > .link {
    max-width: 300px;

    display: block;
    margin-bottom: 1rem;

    color: #444444;
    font-size: 2rem;
    text-align: center;
    word-break: break-word;
  }
}

@media (max-width: 600px) {
  .container {
    height: auto;
  }
}



