.formAction {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}

.formAction > .prev,
.formAction > .next {
  outline: 0;
  border-radius: 4px;
  border: 0;
  padding: .7rem 8rem;
  color: white;
  cursor: pointer;
}

.formAction > .prev {
  border: 1px solid #dddddd;
  margin: 0 2rem 0 0;
  color: #777777;
  background-color: white;
}

.formAction > .prev[disabled] {
  cursor: not-allowed;
  opacity: 0.4;
}

.formAction > .next {
  background-color: #6177B7;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
}

.formAction > .next[disabled] {
  cursor: not-allowed;
  opacity: 0.4;
}

.portUrl {
  margin-top: -30px;
  margin-bottom: 50px;
}

.warningText {
  color: red;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .formAction {
    flex-flow: column;
  }
  .formAction > .next,
  .formAction > .prev {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    text-align: center;
  }
  .formAction > .next {
    order: 1;
    margin: 0 0 3rem;
  }
  .formAction > .prev {
    order: 2;
    margin: 0;
  }
}

.input_wrapper {
  overflow: auto;
}

.btn_wrapper {
  margin-top: 30px;
  margin-bottom: 40px;
}

.input_text {
  margin: 1rem 0 0;
}

.dropDown {
  width: 100%;
  background: white;
  height: 40px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.btn_file {
  position: relative !important;
  overflow: hidden;
}

input {
  height: 50px;
}

.profile {
  text-align: center;
  margin-bottom: 40px;
}

.profile .input {
  position: absolute;
  top: 0;
  right: 0;
  text-align: right;
  filter: alpha(opacity=0);
  opacity: 0;
  outline: none;
  background: red;
  cursor: inherit;
  display: inline-block;
}

.profile .image {
  width: 150px;
  height: 150px;
  border: 1px solid #dedede;
  border-radius: 75px;
  background-color: #dedede;
  margin-left: auto;
  margin-right: auto;
  display: block;
  margin-bottom: 30px;
}

.port {
  text-align: left;
}

.port .portName {
  margin-left: 20px;
}

.port .btn_file {
  margin-right: 15px;
}

.flags {
  position: absolute;
  right: 20px;
  top: 7px;
}

.flags > * {
  margin: 0 2px;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  color: white;
  float: right;
  text-align: center;
}

.flags .active {
  background: linear-gradient(#cc0, #aa0);
  color: black;
}

.flags .dirty {
  background: linear-gradient(#090, #060);
}

.flags .visited {
  background: linear-gradient(#009, #006);
}

.flags .touched {
  background: linear-gradient(#099, #066);
}

.radioLabel {
  margin: 0 25px 0 5px;
}

.cog {
  position: absolute;
  left: 0;
  top: 10px;
}

.logo {
  display: block;
  margin: 4rem auto;
  max-width: 300px;
  width: 100%;
}

.major_text {
  color: #f9e03d;
}

.form6_wrapper {
  margin-left: -40px;
}

.error_response {
  margin-top: -40px;
  margin-bottom: 40px;
  color: red;
}



