:global {
  #content {
    overflow-x: hidden;
  }
}

.ribbon {
  width: auto;
  position: fixed;
  bottom: 0rem;
  left: 0rem;
  z-index: 99999;
}

@media (max-width: 768px) {
  .container {
    padding-top: 50px;
  }

  .ribbon {
    width: 70px;
  }
}



