.container {
  border-top: 1px solid #C9CACB;
  border-bottom: 1px solid #C9CACB;
  padding: 40px 0 80px;

  background: #F5F8F9;
}

.title {
  margin-bottom: 40px;
  text-align: center;
}

.programs {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

@media (max-width: 768px) {
  .container {
    padding: 40px 0;

    .programs {
      flex-flow: column nowrap;
    }
  }
}



