.title {
  margin-top: 3rem;
  margin-bottom: 3rem;
  width: 100%;

  font-family: 'Prompt', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
}

.sub {
  margin: 0 0 .5rem 0;

  font-size: 1.7rem;
  font-family: sans-serif;
  font-weight: 300;
}

.inputGroup {
  margin: 0 0 4rem 0;

  max-width: 100%;
  width: 640px;

  > .label {
    margin: 0 0 .5rem;
    display: block;

    font-size: 2rem;
    font-weight: normal;
  }

  .input,
  .textArea {
    margin: 0 0 .5rem;
    border-radius: 2px;
    border: 1px solid #ccc;
    padding: .5rem .8rem;
    max-width: 100%;
    width: 100%;
    height: auto;

    font-size: 1.5rem;

    &:active,
    &:focus {
      outline: 0;
      border: 1px solid #5D73B3;
    }
  }

  > .textArea {
    min-height: 150px;
  }
}

.confirm {
  display: block;
  margin: 0 auto;

  border-radius: 4px;
  border: 0;
  padding: 1.5rem;
  max-width: 360px;
  width: 100%;

  color: white;
  font-size: 2rem;
  text-align: center;

  background-color: #5D73B3;

  &:hover {
    background-color: darken(#5D73B3, 15%);
    text-decoration: none;
    color: white;
  }

  &[disabled] {
    opacity: .5;
    cursor: not-allowed;
  }
}

.line {
  margin-top: 0;
  width: 100%;
  border: 0;
  border-top: 1px solid #cccccc;

}

.subtitle {
  margin-bottom: 2rem;

  width: 100%;

  font-size: 2.5rem;
  font-weight: 300;
  text-align: center;
}

.bold {
  color: #5D73B3;
  font-weight: bold;
}



